import Vue from 'vue'
import Vuex from 'vuex'
import axios from 'axios'
Vue.use(Vuex)
const state = {
	currentUser: "",
	isLogin: false,
	token: "",
	allData: [],
	drawer: false,
}
const getters = {

}
const mutations = {
	setUser(state, user) {
		if (user) {
			sessionStorage.setItem("userName", user.userName);
			sessionStorage.setItem("usertoken", user.userToken);
			state.currentUser = user.userName;
			state.isLogin = true;
			state.token = user.userToken;
		} else if (user == null) {
			sessionStorage.setItem("userName", null);
			sessionStorage.setItem("usertoken", null);
			state.currentUser = null;
			state.isLogin = false;
			state.token = "";
		}
	},
	getAllData(state, data) {
		state.allData = data;
	},
	openDrawer(state) {
		state.drawer = true;
	},
	closeDrawer(state){
		state.drawer = false;
	}
}
const actions = {
	setAllData({commit}) {
		axios.get("/api/getAllData").then(data => {
			commit("getAllData", data.data.data);
		})
	}
}

export const store = new Vuex.Store({
		state,
		getters,
		mutations,
		actions
	}

);