<template>
  <!--一定要给宽高，不然会报错，container没有高度-->
  <div id="chart">echarts百度地图</div>
</template>

<script>
import BmapJSON from "../../assets/content/mapdata.json";
export default {
  data() {
    return {};
  },
  mounted() {
    this.initMap();
  },
  methods: {
    initMap() {
      let myChart = this.$echarts.init(document.getElementById("chart"));
      let pointsData = BmapJSON; //其实这一步是多余的
      //把数据处理成需要的格式，只是把数值都改成了1，具体项目可以根据自己实际来处理
        var points = pointsData.map(function(track) {
          return {
            coords: track.map(function(seg, idx) {
              return seg.coord;
            })
          };
        });
      myChart.setOption({
        animation: false,
        bmap: {
          center: [120.030551, 30.223874],
          zoom: 18,
          roam: true
        },
        series: [
          {
            type: "lines",
            coordinateSystem: "bmap",
            data: points,
            polyline: true,
            lineStyle: {
              normal: {
                color: {
                  type: "linear",
                  x: 0,
                  y: 0,
                  x2: 0,
                  y2: 1,
                  colorStops: [
                    {
                      offset: 0,
                      color: "green" // 0% 处的颜色
                    },
                    {
                      offset: 1,
                      color: "green" // 100% 处的颜色
                    }
                  ],
                  global: false // 缺省为 false
                },
                opacity: 0.6,
                width: 3
              }
            }
          }
        ]
      });
    }
  }
};
</script>

<style>
#chart {
  width: 100%;
  height: 100%;
  /* width: 500px;
  height: 500px; */
